<template>
  <div id="box">
    <!-- <header>
        <div class="back">

        </div>
        <div class="header-title">
           {{title}}
        </div>
        <div class="header-more"></div>
    </header> -->
    <div class="content">
        <div class="section">
            <div class="item">
                <label>
                    卡券类型<span>*</span>
                </label>
                <span>
                    <Combobox  :init='cardTypeInit' :type="Type">
                        <div slot="box" style="overflow:hidden;" @click.stop="rt">
                            <div class="pup" @click.stop="rt">请选择分类</div>
                            <ul class="listul">
                                <li :class="cardTypeInit.value==item.value?'active':''" v-for="(item,i) in cardTypeList" :key="i" :data-key="cardTypeInit.value==item.value?0:1" :data-index="i" :data-value="item.value" @click.stop="cardChangFn($event,item)">{{item.text}}</li>
                            </ul>
                            <div class="queding" @click.stop="rt">
                                <button @click.stop="okCradTypeList">确定</button>
                            </div>
                        </div>
                    </Combobox>
                </span>
            </div>
            <div class="item" v-if="amtype==1">
                <label for="cardValue">
                    卡券面值<span>*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="number" v-validate="{required:true,num:true}" name="cardValue" id="cardValue" placeholder="请输入卡券面值" v-model="cardValue"/>
                    <i class="tip" v-show="errors.has('cardValue')">{{errors.first('cardValue')}}</i>
                </span>
            </div>
            <div class="item" v-else>
                <label for="cardValue">
                    卡券折扣<span>*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="number" v-validate="{required:true,maxmin:true}" name="cardValue" id="cardValue" placeholder="1-9.9" v-model="cardValue"/>
                    <i class="tip" v-show="errors.has('cardValue')">{{errors.first('cardValue')}}</i>
                </span>
            </div>
            <div class="item noneBor">
                <label for="cardTitle">
                    卡券标题<span>*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="text" v-validate="'required'" name="cardTitle" id="cardTitle" placeholder="请输入活动标题" v-model="cardTitle"/>
                    <i class="tip" v-show="errors.has('cardTitle')">{{errors.first('cardTitle')}}</i>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label>
                    卡卷封面<span>*</span>
                </label>
                <span class="flex-box">
                    <div class="goods-img" v-for="(item,i) in coversImg" :key="i">
                        <!-- <i :class="coversImgIndex==i?'false':''" :data-id="item.id"></i> -->
                        <img :src="item.dataurl==''?item.url:item.dataurl" :alt="item.alt" @click="Type==3?rt():previewImg(item,i)">
                    </div>
                    <div v-if="Type==3?false:true" class="goods-img" @click="upFileInfo.key=true">
                        <img src="../assets/icon-upimg.png" @click="upFileInfo.key=true">
                        <span style="font-size:14px;color:red;">建议上传正方形图片</span>
                    </div>
                </span>
            </div>
            <div class="item">
                <label>
                    使用时间<span>*</span>
                </label>
                <span class="goods-fill-in start">
                    <div class="fle">
                        <button @click="Type==3?rt():startTime()" :data-date="st">{{starttime}}</button>
                    </div>
                    <div class="slimw fle">
                        至
                    </div>
                    <div class="fle">
                        <button class="right" @click="Type==3?rt():endTime()" :data-date="et">{{endtime}}</button>
                    </div>
                    <i class="btomr"></i>
                </span>
            </div>
            <div class="item">
                <label>
                    领券数量
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="number" name="getCardNum" id="getCardNum" placeholder="不填默认为1" v-model="getCardNum"/>
                </span>
            </div>
            <div class="item">
                <label for="condition">
                    使用条件<span v-if="cardTypeInit.value!=4">*</span>
                </label>
                <span v-if="cardTypeInit.value!=4">
                    <input :disabled="Type==3?true:false" type="number" v-validate="'required|num'" name="condition" id="condition" placeholder="满多少金额可以使用" v-model="condition"/>
                    <i class="tip" v-show="errors.has('condition')">{{errors.first('condition')}}</i>
                </span>
                <span v-if="cardTypeInit.value==4">
                    <input :disabled="Type==3?true:false" type="number" name="condition" id="condition" placeholder="满多少金额可以使用"
                        v-model="condition" />
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label for="place">
                    使用场所<span>*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="text" v-validate="'required'" name="place" id="place" placeholder="请输入卡券使用场所" v-model="place"/>
                    <i class="tip" v-show="errors.has('place')">{{errors.first('place')}}</i>
                </span>
            </div>
            <div class="item" v-if="Type==3?true:false">
                <label for="lingQuNum">
                    领取数量<span>*</span>
                </label>
                <span>
                    <input :disabled="true" type="number"  name="lingQuNum" id="lingQuNum" v-model="lingQuNum"/>
                </span>
            </div>
            <div class="item" v-if="Type==3?true:false">
                <label for="usedNum">
                    使用数量<span>*</span>
                </label>
                <span>
                    <input :disabled="true" type="number"  name="usedNum" id="usedNum" v-model="usedNum"/>
                </span>
            </div>
            <div class="item">
                <label for="instruction">
                    卡券说明<span>*</span>
                </label>
                <span>
                    <textarea :disabled="Type==3?true:false" v-validate="'required'" name="instruction"  id="instruction" placeholder="请输入卡券使用说明" cols="30" rows="4" v-model="instruction"></textarea>
                    <i class="tip" v-show="errors.has('instruction')">{{errors.first('instruction')}}</i>
                </span>
            </div>
            <div class="item noflex1">
                <label>
                    有限时间<span>*</span>
                </label>
                <span class="flex-box">
                   <div :class="['goods-color',xhClass(item)?'active':'']"  v-for="(item,i) in limitedDate" :data-key="xhClass(item)?0:1" :data-id="item.id" :key="i" @click="Type==3?rt():weekKeyFn($event,item)">
                       {{item.name}}
                   </div>
                </span>
            </div>
            <div class="item">
                <label>
                   有限时段
                </label>
                <span>
                    <div class="time-add" v-if="Type==3?false:true">
                        <button @click="addTime">添加</button>
                        <span>（例如：00:00至23:59）</span>
                    </div>
                    <div class="time-c-box">
                        <div class="time-item" v-for="(item,i) in limitedTime" :key="i">
                            <input :disabled="Type==3?true:false" type="text" :value="item['one']" @blur="regTime($event,i)"/>
                            <span>至</span>
                            <input :disabled="Type==3?true:false" type="text" :value="item['two']" @blur="regTime1($event,i)"/>
                            <i v-if="Type==3?false:true" :data-id="item.id" @click.stop="removeTime(item.id)"></i>
                        </div>
                    </div>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item submintItem">
                <button v-if="Type==1" :disabled="disabled" class="button" @click="ok">确定</button>
                <button v-if="Type==2" :disabled="disabled" class="button" @click="ok">保存</button>
            </div>
        </div>
    </div>
    <image-html5-upload @imgIndex="changeIndex" imgNumLimit="1" :init="upFileInfo" v-if="upFileInfo.key" :parentImgArr="coversImg"></image-html5-upload>
    <!-- <upfile :init="upFileInfo" :imgArr="coversImg" @imgIndex="changeIndex" :addType="1"></upfile> -->
    <alert-img :init="imgPreview" :imgArr="coversImg" @indexChange="indexChange" @remove="imgChange" :autobutton="false"></alert-img>
    <alert-time :init="limitTimeInfo">
        <div class="time-center" slot="timebox">
            <div class="t-header">
                选择时间
            </div>
            <div class="t-content">
                <input type="button" ref="cot" value="00:00" @click.stop="checkOneTime($event)"/>
                <span>至</span>
                <input type="button" ref="ctt" value="23:59" @click.stop="checkTwoTime($event)"/>
            </div>
            <div class="t-footer">
                <button @click.stop="timeClose">取消</button>
                <button @click.stop="timeOk">确定</button>
            </div>
        </div>
    </alert-time>
    <mt-datetime-picker
        ref="date"
        type="date"
        :startDate="new Date('2010-01-01')"
        :endDate="endDate"
        v-model="startTime1"
        @confirm="handStrat"
        >
    </mt-datetime-picker>
    <mt-datetime-picker
        ref="date1"
        type="date"
        :endDate="new Date('2050-01-01')"
        :startDate="startDate"
        v-model="endTime1"
        @confirm="handEnd"
        >
    </mt-datetime-picker>
    <mt-datetime-picker
        ref="picker"
        type="time"
        :endHour="endHour"
        @confirm="handleConfirm"
        >
    </mt-datetime-picker>
    <mt-datetime-picker
        ref="picker1"
        type="time"
        :startHour="startHour"
        :startMinute="startMinute"
        @confirm="handleConfirm1"
        >
    </mt-datetime-picker>
  </div>
</template>
<script>
    
    //import AlertImg from './component/AlertImg.vue';
    let AlertImg = resolve => {
        require.ensure(['./component/AlertImg.vue'], () => {
            resolve(require('./component/AlertImg.vue'))
        })
    }
    //import AlertTime from './component/AlertTime.vue';
    let AlertTime = resolve => {
        require.ensure(['./component/AlertTime.vue'], () => {
            resolve(require('./component/AlertTime.vue'))
        })
    }
    //import Upfile from './component/Upfile.vue';
    // let Upfile = resolve => {
    //     require.ensure(['./component/Upfile.vue'], () => {
    //         resolve(require('./component/Upfile.vue'))
    //     })
    // }
    let h5FileUpVue = resolve => {
        require.ensure(['./component/h5-file-up/h5-file-up.vue'], () => {
            resolve(require('./component/h5-file-up/h5-file-up.vue'))
        })
    }
    // import { MessageBox,Indicator } from 'mint-ui';
    import { Validator } from 'vee-validate';
    import com from '../assets/common.js';
    import Exotic from '../config.js';
    export default{
        components:{
            AlertImg,
            AlertTime,
            // Upfile,
            'image-html5-upload':h5FileUpVue
        },
        created(){
            let that=this;
            const dictionary = {
                zh_CN: {
                    messages: {
                        email: () => '邮箱格式不正确哦',
                        required:(field)=>field+"不能为空",
                        numeric:(field)=>field+"不是数字"
                    },
                    attributes:{
                        cardValue:"数值",
                        cardTitle:'活动标题',
                        getCardNum:'领券数量',
                        condition:'使用条件',
                        place:'使用场所',
                        instruction:'使用说明'
                    }
                }
                
            };
            Validator.localize(dictionary);
            Validator.extend('maxmin', {
                getMessage: field => field + '不在数字范围内',
                validate:value => /^(\d|9)(\.\d)?$/.test(value)
            });
            Validator.extend('num', {
                getMessage: field => field + '请输入大于0的数字',
                validate:value => /^(([0-9]+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*))$/.test(value)
            });
            if(this.$route.query.type!=undefined){
                this.Type=parseInt(this.$route.query.type);
            }
            if(this.$route.query.id!=undefined){
                this.id=parseInt(this.$route.query.id);
            }
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            if(this.Type==1){
                this.title=" 添加卡券";
            }
        },
        mounted(){
            let that=this;
            this.initCardType();
            //加载修改
            if(this.Type==2||this.Type==3){
                that.MintUi.Indicator.open({
                    text: '加载中...',
                    spinnerType: 'fading-circle'
                });
                Exotic.ajaxData(that.token,Exotic.urlMap.coupon.detail, {couponId: that.id}, 'get',function(data){
                    if(that.Type==2){
                        that.title="修改卡券"
                    }else if(that.Type==3){
                        that.title="查看卡券"
                    }
                    console.log(data);
                    let obj={};
                    for(let i=0;i<that.cardTypeList.length;i++){
                        if(that.cardTypeList[i].value==data.coupon.couponType){
                            obj=that.cardTypeList[i];
                            break;
                        }
                    }
                    that.cardTypeInit.value=''+data.coupon.couponType;
                    that.cardTypeInit.text=obj.text;
                    if(data.coupon.couponType !== 2){ //卡卷面值
                        that.amtype=1;
                        that.cardValue=data.coupon.faceValue;
                    }else if(data.coupon.couponType == 2){//卡卷折扣
                        that.cardValue=data.coupon.discount; 
                        that.amtype=2;
                    }
                    that.cardTitle=data.coupon.title; //卡卷标题
                    try {
                        that.lingQuNum=parseInt(data.coupon.couponNum-data.coupon.deliveryNum);//领取数量
                        that.usedNum=data.coupon.usedNum; //使用数量
                    } catch (error) {
                        
                    }
                    //开始时间
                    that.st=data.coupon.startTime;
                    that.starttime=com.getTimes(data.coupon.startTime+'','YY-MM-DD');
                    that.startTime1=new Date(data.coupon.startTime);
                    //结束时间
                    that.et=data.coupon.endTime;
                    that.endtime=com.getTimes(data.coupon.endTime+'','YY-MM-DD');
                    that.endTime1=new Date(data.coupon.endTime);
                    that.getCardNum=data.coupon.limitNum; //限领数量

                    that.condition=data.coupon.useCondition; //使用条件
                    that.imgPreview.id=new Date().getTime();
                    that.imgPreview.url=Exotic.imageUrl + data.coupon.logo//图片路径
                    that.imgPreview.index=0;
                    that.coversImg.push({
                        id:that.imgPreview.id,
                        alt:'说明',
                        dataurl:'',
                        url:Exotic.imageUrl + data.coupon.logo
                    });
                    that.place=data.coupon.usePlace; //使用场所
                    that.instruction=data.coupon.remark; //卡卷说明
                    //-----------------有效时间周期------------------------
                    var weeks_arr = data.coupon.weeks;
                    weeks_arr = weeks_arr.split(","); //每周有效时间
                    that.weekKey=weeks_arr;
                    //------------------有效时间-时-分-----------------------
                    var times_arr = data.coupon.times; //有效时间
                    times_arr = times_arr.split(";");
                    console.log(times_arr);
                    that.limitedTime=[];
                    for(var i=0;i<times_arr.length;i++){
                        let ti=times_arr[i].split(',');
                        that.limitedTime.push({
                            id:new Date().getTime(),
                            one:ti[0],
                            two:ti[1]
                        })
                    }
                    that.MintUi.Indicator.close();
                });
            }
        },
        data(){
            return {
                title:'',
                //卡券类型数据
                cardTypeList:[
                    {'value':'0','text':'请选择'},
                    {'value':'1','text':'代金券'},
                    {'value':'2','text':'折扣券'},
                    {'value':'3','text':'优惠券'},
                    {'value':'4','text':'兑换券'}
                ],//卡券类型默认
                cardTypeInit:{
                    value:'0',//默认选中的卡券类型
                    id:'spfl',//id
                    key:false,//弹出层控制
                    text:''//默认选中的卡券类型文字
                },
                id:'',
                lingQuNum:0,
                usedNum:0,
                Type:1,
                disabled:false,
                cardValue:'',//卡券面值
                cardValuez:'',//卡券类型
                amtype:1, //卡券面值折扣切换
                cardTitle:'',//卡券标题
                getCardNum:1,//领券数量
                condition:'',//使用条件
                place:'',//使用场所
                instruction:'',//使用说明                
                coversImgIndex:0,//商品图片的默认选中索引
                starttime:'开始时间',
                endtime:'结束时间',
                et:'',
                st:'',
                startTime1:new Date(),
                endTime1:new Date(),
                //最新日期可选
                startDate:new Date('2010-01-01'),
                endDate:new Date('2050-01-01'),
                //图片预览
                imgPreview:{
                    key:false,
                    index:0,
                    id:'',
                    url:''
                },
                //上传
                upFileInfo:{
                    key:false,
                    id:'123'
                },
                //图片列表
                coversImg:[
                ],
                //有限时间选中
                weekKey:[],
                //有限时间
                limitedDate:[
                    {name:'星期一',id:'1'},
                    {name:'星期二',id:'2'},
                    {name:'星期三',id:'3'},
                    {name:'星期四',id:'4'},
                    {name:'星期五',id:'5'},
                    {name:'星期六',id:'6'},
                    {name:'星期日',id:'7'}
                ],
                pickerValue:'',
                startHour:0,//最少小时
                startMinute:0,//最小分钟
                endHour:23,
                limitTimeInfo:{
                    key:false
                },
                limitedTime:[
                    {
                        id:new Date().getTime(),
                        one:"00:00",
                        two:"23:59"
                    }
                ]
            }
        },
        methods:{
            xhClass(item){
                let re=false;
                for(let i=0;i<this.weekKey.length;i++){
                    if(this.weekKey[i]==item.id){
                        re=true;
                    }
                }
                return re;
            },
            //开始时间
            startTime(){
                this.$refs.date.open();
            },
            //结束时间
            endTime(){
                this.$refs.date1.open();
            },
            handStrat(res){
                console.log(typeof res);
                this.starttime=com.getTimes(res,'YY-MM-DD');
                this.startDate=res;
                this.st=new Date(res).getTime();
            },
            handEnd(res){
                this.endtime=com.getTimes(res,'YY-MM-DD');
                this.endDate=res;
                this.et=new Date(res).getTime();
            },
            //取消时间选择
            timeClose(){
                this.limitTimeInfo.key=false;
            },
            //确定时间选择
            timeOk(){
                let id=new Date().getTime()+parseInt(Math.random()*10000+1000);
                this.limitedTime.push({
                    id:id,
                    one:this.$refs.cot.value,
                    two:this.$refs.ctt.value
                });
                this.limitTimeInfo.key=false;
            },
            //弹出第一个时间
            checkOneTime(ev){
                this.$refs.picker.open();
            },
            //弹出第二个时间
            checkTwoTime(ev){
                this.$refs.picker1.open();
            },
            //添加时间按钮
            addTime(){
                this.limitTimeInfo.key=true;
                this.startHour=0;
                this.startMinute=0;
            },
            //选择第一个时间
            handleConfirm(res){
                this.$refs.cot.value=res;
                //this.$refs.ctt.value=res;
                let minH=parseInt(res.split(':')[0]);
                let minM=parseInt(res.split(':')[1]);
                this.startMinute=minM;
                this.startHour=minH;
            },
            //选择第二个时间
            handleConfirm1(res){
                this.$refs.ctt.value=res;
                let minH=parseInt(res.split(':')[0]);
                this.endHour=minH;
            },
            //删除时间
            removeTime(id){
                let that=this;
                that.MintUi.MessageBox.confirm('确定删除?').then(function(action){
                    let index=com.contains(that.limitedTime,id,'id');
                    that.limitedTime.splice(index,1);
                },function(err){
                    console.log(err);
                });
            },
            //预览图片
            previewImg(item,i){
                this.imgPreview.key=true;
                this.imgPreview.index=i;
                this.imgPreview.id=item.id;
            },
            //设置封面返回
            indexChange(i){
                this.coversImgIndex=parseInt(i);
                this.imgPreview.id=this.coversImg[i].id;
                this.imgPreview.url=this.coversImg[i].url;
                this.imgPreview.index=parseInt(i);
            },
            //弹出防止冒泡
            rt(){
                return true;
            },
            //运算找出对应ID的数据
            stext(arr,obj){
                for(let i=0;i<arr.length;i++){
                    if(arr[i].value==obj.value){
                        return arr[i];
                    }
                }
                /*return arr.find((item)=>{
                    return item.value == obj.value;
                });*/
            },
            //卡券类型变更
            cardChangFn(ev,res){
                this.cardTypeInit.value=res.value;
            },
            //确认卡券类型
            okCradTypeList(){
                let text=this.stext(this.cardTypeList,this.cardTypeInit).text;
                this.cardTypeInit.text=text;
                this.cardTypeInit.key=false;
                if(this.cardTypeInit.value==2){
                    this.amtype=2;
                }else{
                    this.amtype=1;
                }
            },
            //有限时间选择
            weekKeyFn(ev,item){
                let key = ev.target.dataset.key;
                let id = ev.target.dataset.id;
                if(key==1){
                    this.weekKey.push(item.id);
                    ev.target.className='goods-color active';
                    ev.target.dataset.key="0"
                }else{
                    let index=com.contains(this.weekKey,id);
                    this.weekKey.splice(index,1);
                    ev.target.className='goods-color';
                    ev.target.dataset.key="1"
                }
            },
            //有效时段的正则
            regTime(ev,i){
                if(/^(([1-9]{1})|([0-1][0-9])|([1-2][0-3])):([0-5][0-9])$/.test(ev.target.value)){
                    this.disabled=false;
                    this.limitedTime[i].one=ev.target.value;
                }else{
                    Exotic.tip({txt:"请输入正确的时间格式"});
                    ev.target.focus();
                    this.disabled=true;
                }
            },
            regTime1(ev,i){
                if(/^(([1-9]{1})|([0-1][0-9])|([1-2][0-3])):([0-5][0-9])$/.test(ev.target.value)){
                    this.disabled=false;
                    this.limitedTime[i].two=ev.target.value;
                }else{
                    Exotic.tip({txt:"请输入正确的时间格式"});
                    ev.target.focus();
                    this.disabled=true;
                }
            },
            //图片上传返回
            changeIndex(obj){
                console.log('-------------------------图片上传返回------------------------')
                console.log(obj);
                this.coversImgIndex=parseInt(obj.index);
                this.imgPreview.id=this.coversImg[obj.index].id;
                this.imgPreview.url=obj.url;
                this.imgPreview.index=parseInt(obj.index);
            },
            //图片删除
            imgChange(){
                this.imgPreview.id='';
                this.imgPreview.url='';
                this.imgPreview.index=0;
                this.coversImgIndex=0;
            },
            //提交按钮
            ok(){
                console.log('--------------------------vue data数据-------------------------')
                console.log(this.$data);
                let that=this;
                if( that.cardTypeInit.value == 0) {
                    Exotic.tip({txt:"请选择卡券类型"});
                    return false;
                }else if (that.cardValue == '') {
                    if ( that.amtype == 1) {
                        Exotic.tip({txt:"请填写卡券面值"});
                    }else {
                        Exotic.tip({txt:"请填写卡券折扣"});
                    }
                    return false;
                }else if (isNaN(that.cardValue)) {
                    if ( that.amtype == 1) {
                        Exotic.tip({txt:"卡券面值不是数字"});
                    }else {
                        Exotic.tip({txt:"卡券折扣不是数字"});
                    }
                    return false;
                }else if (that.amtype==2&&!(/^(\d|9)(\.\d)?$/.test(that.cardValue))) {
                    Exotic.tip({txt:"卡券折扣数值不对"});
                    return false;
                }else if (that.amtype==1&&!(/^(([0-9]+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*))$/.test(that.cardValue))) {
                    Exotic.tip({txt:"卡券面值数值不对"});
                    return false;
                }else if (that.cardTitle == '') {
                    Exotic.tip({txt:"请输入卡券标题"});
                    return false;
                }else if (that.imgPreview.url == '') {
                    Exotic.tip({txt:"请上传封面图片"});
                    return false;
                }else if (that.st == '') {
                    Exotic.tip({txt:"请选择开始时间"});
                    return false;
                }else if (that.et == '') {
                    Exotic.tip({txt:"请选择结束时间"});
                    return false;
                }else if (that.place == '') {
                    Exotic.tip({txt:"请输入使用场所"});
                    return false;
                }else if (that.instruction == '') {
                    Exotic.tip("请输入卡券说明");
                    return false;
                }else if (that.weekKey.length==0) {
                    Exotic.tip({txt:"请选择有限时间"});
                    return false;
                }else if (that.limitedTime.length==0){
                    Exotic.tip({txt:"请选择有限时段"});
                    return false;
                }else{
                    if(that.cardTypeInit.value == 4){
                        if (that.condition==""){
                            that.condition=0
                        }
                    }else if(that.condition == '' || !(/^(([0-9]+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*))$/.test(that.condition))) {
                        Exotic.tip({ txt: "请输入正确使用条件" });
                        return false;
                    }
                    var faceValue, discount, num = 1;
                    if (that.getCardNum == '') {
                        num = parseInt(1);
                    }else{
                        num=parseFloat(that.getCardNum);
                    }
                    if (that.amtype == 1) {
                        discount = '';
                        faceValue = that.cardValue;
                    } else {
                        discount = that.cardValue;
                        faceValue = '';
                    }
                    //裁剪每天有效时间段的调用
                    var times ='';
                    for(let i in that.limitedTime){
                        if(i==that.limitedTime.length-1){
                            times+=that.limitedTime[i].one+","+that.limitedTime[i].two+';';
                            break;
                        }
                        times+=that.limitedTime[i].one+","+that.limitedTime[i].two+';';
                    }
                    times=times.substring(0,times.length-1);
                    //裁切每周有效时间的调用
                    var weeks = that.weekKey.join(',');
                    if(that.Type==1){
                        var param = {
                            title: that.cardTitle,
                            logo: that.imgPreview.url.replace(Exotic.imageUrl,''),
                            couponType: that.amtype,
                            startTime: that.starttime,
                            endTime: that.endtime,
                            weeks: weeks,
                            times: times,
                            remark: that.instruction, //优惠券说明
                            faceValue: faceValue,
                            discount: discount,
                            useCondition: that.condition, //使用条件
                            usePlace: that.place, //使用场所
                            limitNum: num //限领数量
                        };
                        console.log('-------------------------卡券提交参数----------------------');
                        console.log(param);
                        that.MintUi.Indicator.open({
                            text: '提交中...',
                            spinnerType: 'fading-circle'
                        })
                        Exotic.ajaxData(that.token,Exotic.urlMap.coupon.add, param, 'post', function(data){
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"添加卡券成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=12";
                            }
                        });
                    }else if(that.Type==2){
                        var param = {
                            couponId: that.id,
                            title: that.cardTitle,
                            logo: that.imgPreview.url.replace(Exotic.imageUrl,''),
                            couponType: that.amtype,
                            startTime: that.starttime,
                            endTime: that.endtime,
                            weeks: weeks,
                            times: times,
                            remark: that.instruction, //优惠券说明
                            faceValue: faceValue,
                            discount: discount,
                            useCondition: that.condition, //使用条件
                            usePlace: that.place, //使用场所
                            limitNum: num //限领数量
                        };
                        console.log('-------------------------卡券修改参数----------------------');
                        console.log(param);
                        that.MintUi.Indicator.open({
                            text: '提交中...',
                            spinnerType: 'fading-circle'
                        })
                        Exotic.ajaxData(that.token,Exotic.urlMap.coupon.update, param, 'post',function(data){
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"修改卡券成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=12";
                            }
                        });
                    }
                }
            },
            //初始化卡券类型
            initCardType(){
                this.cardTypeInit.text=this.stext(this.cardTypeList,this.cardTypeInit).text;
            },
            save(){
                console.log(this.$data)
            }
        }
    }
</script>
<style scoped lang="scss">
@import '../assets/index.scss';
.time-center {
    height: 201px;
    width: 80%;
    position: absolute;
    z-index: 66;
    top: 50%;
    left: 10%;
    border-radius: 3px;
    margin-top: -100px;
    background: #fff;
  }
  .time-center .t-header {
      height: 50px;
      line-height: 50px;
      font-size: 15px;
      text-align: center;
      font-weight: 700;
  }
  .time-center .t-content {
    height: 30px;
    padding: 37px 0;
  }
  .time-center .t-content input {
    height: 30px;
    border:0;
    display: block;
    color: #fff;
    border-radius: 4px;
    text-align: center;
    width: 40%;
    float: left;
    font-size: 14px;
    outline: none;
    background-color: #e56fa6;
  }
  .time-center .t-content input:first-child {
    margin-left:2.5%;
  }
  .time-center .t-content input:last-child {
    margin-right:2.5%;
  }
  .time-center .t-content span {
    display: block;
    float: left;
    line-height: 30px;
    text-align: center;
    width:15%;
    padding: 0 .0;
  }
  .time-center .t-footer {
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    border-top:1px solid #eee;
  }
  .time-center .t-footer button {
    width:50%;
    line-height: 45px;
    float:left;
    text-align: center;
    background: transparent;
    border: 0;
    padding: 0;
    margin: 0;
    font-size: 14px;
    outline: none;
  }
  .time-center .t-footer button:active {
    background: #ddd;
  }
  .time-center .t-footer button:first-child {
    border-radius: 0 0 0 3px;
    border-right: 1px solid #eee;
  }
  .time-center .t-footer button:last-child {
    color: #e56fa6;
    border-radius: 0 0 3px 0;
  }
  .ql-snow .ql-toolbar button svg,
  .quillWrapper .ql-snow.ql-toolbar button svg {
    width: 15px;
    height: 15px;
  }
</style>




